<template>
  <transition name="fade">
    <div class="window-windowWrap">
      <div class="window-maskLayer"></div>
      <div class="window-maskLayerContent">
        <div class="window-container">
          <div class="window-content">
            <div :style="windowStyle">
              <div class="drag-window" :style="windowStyle">
                <div class="content-title">
                  <span>{{config.title}}</span>
                  <span class="close-btn" @click="close"></span>
                  <span v-if="hasMaxBtn" :class="{'max-btn':!isMaxed,'nor-btn':isMaxed}" @click="max"></span>
                </div>
                <div class="content-body">
                  <slot></slot>
                </div>
                <div class="content-toolbar">
                  <i-button type="primary" v-if="hasConfirm" @click="confirm">提交</i-button>
                  <i-button v-if="hasCancel" @click="cancel">取消</i-button>
                  <i-button type="primary" v-if="hasClose" @click="close">关闭</i-button>
                  <i-button type="info" v-if="hasNext" @click="next" ref="nextBtn" :disabled="curr === 2">
                    下一步
                  </i-button>
                  <i-button type="info" v-if="hasPrev" class="toolbar_other" @click="prev">上一步</i-button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
  import IButton from "./IButton"
  export default {
    props: {
      config: Object
    },
    data() {
      if (typeof this.config == "undefined") {
        throw "组件的config属性未配置"
      }

      var hasConfirm = false;
      var hasCancel = false;
      var hasClose = false;
      var hasPrev = false;
      var hasNext = false;
      var hasMaxBtn = false;
      var curr = 0;

      if (typeof this.config.hasMaxBtn != "undefined") {
        hasMaxBtn = this.config.hasMaxBtn;
      }

      if (this.config.type == "add" || this.config.type == "modify") {
        hasConfirm = true;
        hasCancel = true;
      } else if (this.config.type == "rolePermission") {
        hasCancel = true;
        hasConfirm = true;
        hasPrev = true;
        hasNext = true;
      } else {
        hasClose = true;
      }

      return {
        hasConfirm: hasConfirm,
        hasCancel: hasCancel,
        hasClose: hasClose,
        hasPrev: hasPrev,
        hasNext: hasNext,
        curr: curr,
        hasMaxBtn: hasMaxBtn,
        isMaxed: false,
        windowStyle: {
          height: this.config.height,
          width: this.config.width
        }
      }
    },
    created() {
      this.$root.bus.$on('toggleBtn', value => {
        this.$refs.nextBtn.disabled = true;
        this.addCls(value);
      })
    },
    methods: {
      addCls(value) {
        this.curr = value;
      },
      cancel() {
        this.$emit("windowCancel");

        if (this.config.method.cancel && this.config.parent) {
          this.config.method.cancel.call(this.config.parent);
        }
      },
      confirm() {
        this.$emit("windowConfirm");

        if (this.config.method.confirm && this.config.parent) {
          this.config.method.confirm.call(this.config.parent);
        }
      },
      close() {
        this.$emit("windowClose");

        if (this.config.method.close && this.config.parent) {
          this.config.method.close.call(this.config.parent);
        }
      },
      next() {
        if (this.curr < 3) {
          this.curr++;
        } else {
          this.curr = 2;
        }
        this.$emit("windowNext");
        if (this.config.method.next && this.config.parent) {
          this.config.method.next.call(this.config.parent);
        }
      },
      prev() {
        if (this.curr > 0) {
          this.curr--;
          this.$emit("windowPrev");
          if (this.config.method.prev && this.config.parent) {
            this.$refs.nextBtn.disabled = false;
            this.config.method.prev.call(this.config.parent);
          }
        }

      },
      max() {
        if (!this.isMaxed) {
          var position = $(this.$el).find(".drag-window").position();
          this.styleBak = this.windowStyle;
          this.styleBak.left = position.left + "px";
          this.styleBak.top = position.top + "px";

          this.windowStyle = {
            left: "0px",
            top: "0px",
            width: "100vw",
            height: "100vh"
          }

          this.isMaxed = true;
        } else {
          this.windowStyle = this.styleBak;
          this.isMaxed = false;
        }
      }
    },
    beforeDestroy() {
      this.$root.bus.$off('toggleBtn');
    },
    mounted() {
      $(function () {
        $(".drag-window").easydrag();
        $(".drag-window").setHandler("content-title");
      });
    },
    components: {
      IButton
    }
  }
</script>

<style scoped>
  .window-windowWrap {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    z-index: 999;
  }

  .window-maskLayer {
    background-color: rgba(0, 0, 0, 0.5);
    opacity: 0.8;
    -moz-opacity: 0.8;
    filter: alpha(opacity=80);
    position: absolute;
    width: 100%;
    height: 100%;
  }

  .window-maskLayerContent {
    position: absolute;
    height: 100%;
    width: 100%;
    display: table;
  }

  .window-container {
    text-align: center;
    display: table-cell;
    vertical-align: middle;
  }

  .window-content {
    display: inline-block;
    text-align: left;
  }

  .drag-window {
    border: 1px solid #8c939d;
    background: #ffffff;
    border-top: 0px;
    border-radius: 5px;
    position: absolute;
    z-index: 999;
    box-shadow: 0px 0px 55px 4px #555;
    -webkit-box-shadow: 0px 0px 55px 4px #555;
  }

  .content-title {
    border-radius: 5px 5px 0px 0px;
    height: 35px;
    background: #35baf6;
    color: #fff;
    padding: 5px 10px;
    font-size: 15px;
    text-align: left;
  }

  .content-body {
    height: calc(100% - 80px);
    padding: 10px;
    overflow: auto;
  }

  .content-toolbar {
    height: 47px;
    background: white;
    border-radius: 0px 0px 5px 5px;
    padding-top: 10px;
    border-top: 1px solid #dddddd;
  }

  .content-toolbar button {
    height: 27px;
    float: right;
    margin-right: 12px;
    padding: 0px 12px;
  }

  .close-btn, .max-btn, .nor-btn {
    font-family: "iconfont";
    cursor: pointer;
    float: right;
    color: #fff;
    padding: 0px;
    border: 1px solid #ffffff;
    width: 25px;
    height: 25px;
    line-height: 25px;
    text-align: center;
    margin-left: 5px;
  }

  .close-btn:before {
    content: "\e624";
    font-size: 16pt;
    font-weight: bolder;
  }

  .close-btn{
    line-height: 23px;
    padding-bottom: 5px;
  }

  .nor-btn{
    line-height: 23px;
    padding-top:2px;
  }

  .max-btn:before {
    content: "\e8fa";
    font-size: 14pt;
    background-color: transparent;
    color: #fff;
  }

  .nor-btn:before {
    content: "\e8fb";
    font-size: 13pt;
    position: relative;
    top: -2px;
  }

  .close-btn:hover {
    -webkit-transition: background-color 0.5s;
    -moz-transition: background-color 0.5s;
    -ms-transition: background-color 0.5s;
    -o-transition: background-color 0.5s;
    transition: background-color 0.5s;
    background-color: red;
  }

  .nor-btn:hover, .max-btn:hover{
    -webkit-transition: background-color 0.5s;
    -moz-transition: background-color 0.5s;
    -ms-transition: background-color 0.5s;
    -o-transition: background-color 0.5s;
    transition: background-color 0.5s;
    background-color: #2D64B3;
  }

  @-webkit-keyframes rotateIn {
    0% {
      -webkit-transform-origin: center center;
      -webkit-transform: rotate(-200deg);
    }
    100% {
      -webkit-transform-origin: center center;
      -webkit-transform: rotate(0);
    }
  }

  @-moz-keyframes rotateIn {
    0% {
      -moz-transform-origin: center center;
      -moz-transform: rotate(-200deg);
    }
    100% {
      -moz-transform-origin: center center;
      -moz-transform: rotate(0);
    }
  }


</style>



