<template>
  <input class="inputbox" type="text" v-model="inputValue" @click="$emit('click')" @blur="$emit('blur')" @keyup="$emit('keyup')"/>
</template>

<script>
  export default {
    props: {
      value: {
        type: String,
        default: ""
      },
    },
    data() {
      var inputValue = this.value;

      return {
        inputValue: inputValue
      }
    },
    watch: {
      value(newValue) {
        this.inputValue = newValue;
      },
      inputValue(newValue) {
        this.$emit('input', newValue)
      }
    },
    name: "i-inputbox"
  }
</script>

<style scoped>
  .inputbox {
    width: 100%;
    height: 35px;
    outline: none;
    border-radius: 4px;
    border: 1px solid #a9a9a9;
    font-family: "Helvetica", "\5FAE\8F6F\96C5\9ED1", Arial, SimSun, sans-serif;
    color: #606266;
    font-size: 14px;
    padding: 0px 8px;
  }

  .inputbox:focus, .inputbox:hover {
    border-color: #5cadff;
    -webkit-box-shadow: 0px 0px 10px #d6ebff;
    -moz-box-shadow: 0px 0px 10px #d6ebff;
    box-shadow: 0px 0px 10px #d6ebff;
    cursor: text;
  }
</style>



