<template>
  <div class="header">
    <div class="wrap">
      <div class="nav-normal icon-area" :class="{ 'nav-folded' : folded }">
        <div>
          <span>&#xea60;</span>
          <span style="margin-left:15px;">基础框架</span>
        </div>
      </div>
      <div class="header-body">
        <span class="exit" title="退出" @click="exit()"></span>
        <span>&#xe90c</span>
        <span>&#xeca8;</span>
        <span title="修改密码" @click="editPwd">&#xebdf;</span>
        <span>&#xeb6e;</span>
        <span class="user-info" @click="showUser">{{userWindowConfig.form.username}}</span>
        <span class="header-zoom-button" @click="headerZoom()"></span>
      </div>
    </div>

    <my-dialog v-if="showDialog" :message="dialogMessage" @dialogCancel="dialogCancel" @dialogConfirm="dialogConfirm">
    </my-dialog>

    <my-window v-if="userWindowConfig.show" :config="userWindowConfig">
      <i-form>
        <i-form-item label="ID">
          <i-inputbox readonly v-model="userWindowConfig.form.id"></i-inputbox>
        </i-form-item>
        <i-form-item label="账号">
          <i-inputbox readonly v-model="userWindowConfig.form.loginName"></i-inputbox>
        </i-form-item>
        <i-form-item label="姓名">
          <i-inputbox readonly v-model="userWindowConfig.form.username"></i-inputbox>
        </i-form-item>
        <i-form-item label="工号">
          <i-inputbox readonly v-model="userWindowConfig.form.jobNum"></i-inputbox>
        </i-form-item>
        <i-form-item label="电话">
          <i-inputbox readonly v-model="userWindowConfig.form.phone"></i-inputbox>
        </i-form-item>
        <i-form-item label="邮箱">
          <i-inputbox readonly v-model="userWindowConfig.form.email"></i-inputbox>
        </i-form-item>
      </i-form>
    </my-window>

    <!-- 修改密码 -->
    <my-window :config="editPwdConfig" v-if="editPwdConfig.show">
      <i-form>
        <i-form-item label="原密码">
          <i-inputbox v-model="editPwdConfig.form.oldPassword" type="password"></i-inputbox>
        </i-form-item>
        <i-form-item label="新密码">
          <i-inputbox v-model="editPwdConfig.form.password" type="password"></i-inputbox>
        </i-form-item>
        <i-form-item label="确认密码">
          <i-inputbox v-model="editPwdConfig.form.rePwd" type="password"></i-inputbox>
        </i-form-item>
      </i-form>
    </my-window>
    <my-tooltip :show="showTooltip" :showType="tooltipType" :message="tooltipMessage" @tooltipClosed="tooltipClosed"></my-tooltip>
  </div>
</template>

<script>
import MyDialog from './common/Dialog.vue';
import MyWindow from './common/Window.vue';
import MyTooltip from './common/OperationStatusTooltip.vue';
import IForm from './common/IForm';
import IFormItem from './common/IFormItem';
import IInputbox from './common/IInputbox';

export default {
  data() {
    return {
      showTooltip: false,
      tooltipType: '',
      tooltipMessage: '',
      showDialog: false,
      dialogMessage: '',
      folded: false,
      editPwdConfig: {
        type: 'modify',
        parent: this,
        show: false,
        title: '',
        height: '280px',
        width: '500px',
        form: {
          id: '',
          loginName: '',
          oldPassword: '',
          rePwd: '',
          password: ''
        },
        method: {
          confirm() {
            if (
              '' == this.editPwdConfig.form.oldPassword ||
              '' == this.editPwdConfig.form.password ||
              '' == this.editPwdConfig.form.rePwd
            ) {
              this.showTooltip = true;
              this.tooltipType = 'failed';
              this.tooltipMessage = '表单填写不完整';
              return false;
            }
            if (this.editPwdConfig.form.rePwd != this.editPwdConfig.form.password) {
              this.showTooltip = true;
              this.tooltipType = 'failed';
              this.tooltipMessage = '密码不一致';
              return false;
            }
            this.$http.post('./restful/user/editPwd', this.editPwdConfig.form).then(
              res => {
                if ('success' == res.body.status) {
                  this.showTooltip = true;
                  this.tooltipType = 'success';
                  this.editPwdConfig.show = false;
                  this.tooltipMessage = '修改成功';
                } else {
                  this.showTooltip = true;
                  this.tooltipType = 'failed';
                  this.tooltipMessage = res.body.message;
                }
              },
              res => {
                this.showTooltip = true;
                this.tooltipType = 'failed';
                this.tooltipMessage = '修改失败';
              }
            );
          },
          cancel() {
            this.editPwdConfig.show = false;
          },
          close() {
            this.editPwdConfig.show = false;
          }
        }
      },
      userWindowConfig: {
        type: 'view',
        parent: this,
        show: false,
        title: '用户信息',
        height: '400px',
        width: '560px',
        form: {
          loginName: '',
          id: '',
          username: '',
          jobNum: '',
          phone: '',
          email: ''
        },
        method: {
          close() {
            this.userWindowConfig.show = false;
          }
        }
      }
    };
  },
  components: {
    MyDialog: MyDialog,
    MyWindow: MyWindow,
    MyTooltip,
    IForm,
    IFormItem,
    IInputbox
  },
  methods: {
    // 修改密码
    editPwd() {
      this.editPwdConfig.show = true;
      this.editPwdConfig.title = '修改密码-' + this.userWindowConfig.form.username;
      this.editPwdConfig.form = {
        id: this.userWindowConfig.form.id,
        loginName: this.userWindowConfig.form.loginName,
        oldPassword: '',
        rePwd: '',
        password: ''
      };
    },
    tooltipClosed() {
      this.showTooltip = false;
    },
    showUser() {
      this.userWindowConfig.show = true;
    },
    dialogConfirm() {
      this.$http.get('./restful/user/logout').then(res => {
        if ('success' == res.body.status) {
          window.location = './login.html';
        }
      });
    },
    dialogCancel() {
      this.showDialog = false;
    },
    headerZoom() {
      if (this.folded) {
        //触发导航条的展开事件
        this.$root.bus.$emit('UnfoldNavBar');
      } else {
        //触发导航条的折叠事件
        this.$root.bus.$emit('FoldNavBar');
      }
    },
    exit() {
      this.showDialog = true;
      this.dialogMessage = '确定退出系统吗？';
    }
  },
  mounted() {
    var vue = this;

    this.$root.bus.$on('FoldNavBar', function() {
      vue.folded = true;
    });

    this.$root.bus.$on('UnfoldNavBar', function() {
      vue.folded = false;
    });

    var user = this.$root.tools.currentUser();
    this.userWindowConfig.form.username = user.username;
    this.userWindowConfig.form.id = user.id;
    this.userWindowConfig.form.loginName = user.loginName;
    this.userWindowConfig.form.jobNum = user.jobNum;
    this.userWindowConfig.form.phone = user.phone;
    this.userWindowConfig.form.email = user.email;
  }
};
</script>

<style scoped>
.icon-area {
  display: table-cell;
  height: 100%;
  background-color: #35baf6;
  color: #fff;
  font-size: 20px;
  padding-left: 15px;
  white-space: nowrap;
  overflow: hidden;
  vertical-align: middle;
}

.header-body {
  display: table-cell;
  vertical-align: middle;
  color: #939393;
}

.header-body span {
  font-size: 16pt;
  float: right;
  margin-right: 20px;
  cursor: pointer;
  padding: 5px;
}

span.header-zoom-button {
  float: left;
  margin-left: 10px;
  padding: 5px;
}

.header-zoom-button:before {
  content: '\e90d';
  font-size: 17pt;
}

.exit:before {
  content: '\e90a';
}

span.user-info {
  font-size: 10pt;
  font-family: sans-serif;
  line-height: 14px;
}

.user-info:before {
  content: '\EA1D';
  font-family: 'iconfont';
  font-size: 16pt;
  position: relative;
  left: 0px;
  top: 3px;
}

.header-body span:hover {
  background-color: #f8f8f8;
}
</style>



