<template>
  <span class="normal" @click.stop="toggleSwitch"
        :class="checked?'switch-on switch-on-background':'switch-off switch-off-background'">
    <span class="switch-text switch-on-text" v-show="checked">
      {{onText}}
    </span>
    <span class="switch-text switch-off-text" v-show="!checked">
      {{offText}}
    </span>
  </span>
</template>

<script>
    export default {
      name: "i-switch",
      props: {
        value: {
          type: [Boolean, String, Number],
          default: true
        },
        onText: {
          type: String,
          default: "开"
        },
        offText: {
          type: String,
          default: "关"
        },
        activeValue: {
          type: [Boolean, String, Number],
          default: true
        },
        inactiveValue: {
          type: [Boolean, String, Number],
          default: false
        }
      },
      data() {
        return {
          checked: this.activeValue ? this.activeValue == this.value ? true : false : this.value
        }
      },
      methods: {
        toggleSwitch() {
          this.checked = !this.checked;
          if (this.activeValue) {
            this.$emit("input", this.checked ? this.activeValue : this.inactiveValue);
            this.$emit("change", this.checked ? this.activeValue : this.inactiveValue);
          } else {
            this.$emit("input", this.checked);
            this.$emit("change", this.checked);
          }
        }
      },
      watch: {
        value(newVal) {
          this.checked = this.activeValue ? this.activeValue == newVal ? true : false : newVal;
        }
      },
    }
</script>

<style scoped>
  .normal {
    display: inline-block;
    width: 44px;
    height: 24px;
    border-radius: 24px;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    line-height: 22px;
    vertical-align: middle;
    position:relative;
  }
  .normal:before {
    content: "";
    width: 20px;
    border-radius: 50%;
    background-color: #ffffff;
    cursor: pointer;
    position: absolute;
    bottom: 1px;
    top: 1px;
  }
  .switch-on:before {
    right: 1px;
  }
  .switch-on-background {
    background: #5cadff;;
  }
  .switch-text {
    position: absolute;
    font-size: 14px;
    color: #ffffff;
    line-height: 24px;
    font-weight: bold;
  }
  .switch-on-text {
    left: 5px;
  }
  .switch-off-text {
    right: 5px;
  }
  .switch-off:before {
    left: 1px;
  }
  .switch-off-background {
    background: lightgray;
  }
</style>



