            {id: "5", name: "襄阳"},
            {id: "6", name: "仙桃"},
            {id: "7", name: "太原"},
            {id: "8", name: "晋城"},
            {id: "9", name: "长治"},
            {id: "10", name: "扬州"},
            {id: "11", name: "苏州"},
            {id: "12", name: "杭州"},
            {id: "13", name: "温州"}
          ]
        },

        // 滑块
        sliderConfig: {
          testSlider: 50
        },

        // 开关
        switchConfig: {
          testSwitch: 0
        },

        // 时间选择器
        timePickerConfig: {
          testTime: "12:45:56"
        },

        // 穿梭框
        transferConfig: {
          transferTitle: ["城市1", "城市2"],
          originalData: [
            {value: 1, label: "武汉"},
            {value: 2, label: "黄石"},
            {value: 3, label: "襄阳"},
            {value: 4, label: "咸宁"},
            {value: 5, label: "荆州"},
            {value: 6, label: "鄂州"},
            {value: 7, label: "长沙"},
            {value: 8, label: "成都"},
            {value: 9, label: "北京"},
            {value: 10, label: "上海"},
            {value: 11, label: "张家界"},
            {value: 12, label: "广州"}
          ],
          testTransfer: [
            {value: 6, label: "鄂州"},
            {value: 7, label: "长沙"}
          ]
        },

        // 消息提示
        tooltipConfig: {
          tooltipType: "",
          tooltipMessage: "",
          showTooltip: false
        },

        // 树形下拉框
        // treeSelectConfig: {
        //   api: "./restful/caorg/getAll",
        //     rows:[{id:'1',parentId:'-1',name:'a'},{id:'2',parentId:'-1',name:'b'}],
        //   field: "name",
        //   selectStyle: {
        //     width: "100%",
        //     height: "32px",
        //     float: "left"
        //   }
        // },

        // 弹窗
        windowConfig: {
          type: "",
          readonly: false,
          show: false,
          title: "",
          form: {
            parentId: "0",
            files: []
          }
        },

        // 步骤弹窗
        stepWindowConfig: {
          show: false,
          active: 1
        },
        // 文本域
        textAreaConfig: {
          testArea: "",
          readonly: false
        },

        // 文本框
        inputConfig: {
          testInput: "",
          readonly: false
        },

        // tab栏
        tabConfig: {
          activeTab: 0,
          style: {
            width: "600px",
            height: "300px"
          }
        },

        //右击鼠标弹框
        DialogItems: [
          '黄色', '紫色', '绿色'
        ],
        menuJson: [
          // 1
          {
            name: "第二个菜单1",
            id: "sec-menu-1",
            callback: function () {
              alert("夏天吃西瓜~");
            }
          },
          {
            name: "第二个菜单2",
            id: "sec-menu-2",
            callback: function () {
              alert("夏天吃香瓜~");
            }
          },
          // 1 end
          // 2
          {
            name: "第二个子菜单1",
            id: "sec-s-menu-1",
            parent: "sec-menu-1",
            callback: function () {
              alert("夏天吃葡萄~");
            }
          },
        ],
        active: 1,
        tabActive: 1
      }
    },
    methods: {
      // 复选框
      checkStatusChange(checked) {
        console.log(this.datePickerConfig.testDate)
        console.log("复选框状态：" + checked);
      },

      // 确认操作提示框
      dialogConfirm() {
        //对值进行重置,不然不会触发change事件
        this.dialogConfig.showDialog = false;
      },
      dialogCancel() {
        //对值进行重置,不然不会触发change事件
        this.dialogConfig.showDialog = false;
      },
      showDialog(type, message) {
        this.dialogConfig = {
          showDialog: true,
          dialogMessage: message,
          type: type
        }
      },
      dialogAlert() {
        this.showDialog("alert", "这是一段提示信息");
      },
      dialogError() {
        this.showDialog("error", "这是一段错误信息");
      },

      // 开关
      changeSwitch(status) {
        console.log("开关状态：" + status);
      },

      // 下拉框
      change(value) {
        console.log(value);
      },

      // 消息提示
      showTooltip(type, message) {
        this.tooltipConfig = {
          showTooltip: true,
          tooltipMessage: message,
          tooltipType: type
        };
      },
      testSuccess() {
        this.showTooltip("success", "成功提示");
      },
      testFail() {
        this.showTooltip("failed", "失败提示");
      },

      // 弹窗
      showWindow(type, title) {
        this.windowConfig.show = true;
        this.windowConfig.title = title;
        this.windowConfig.type = type;
      },
      testWindow() {
        this.showWindow("add", "弹窗测试");
      },
      submit() {
        this.showTooltip("success", "操作成功");
      },

      // 步骤弹窗
      testStepWindow() {
        this.stepWindowConfig.show = true;
        this.stepWindowConfig.active = 1;
        this.windowConfig.form.parentId = "";
      },

      // 文件上传
      getFiles() {
        console.log(this.uploadConfig.files);
      },

      //右击对话框
      dialogFn(index, item) {
        this.$refs.dialog.contextMenu(this.$refs.colors.childNodes[index], this.menuJson, item)
      }
    },
    directives: {
      dialog: {
        bind: function (el, binding, vnode) {
        },
        inserted: function (el, binding, vnode) {
          // console.log(vnode.context)
          var _this = vnode.context
          _this.dialogFn(binding.value.index, binding.value.item)
        },
      }
    },
    mounted() {
      var c = setInterval(()=>{
        this.percentage+=1;
        if(this.percentage == 100){
          clearInterval(c);
        }
      },100);
      // this.$http.get("./restful/caorg/getAll").then(
      //   (res) => {
      //     this.selectConfig.options = res.body.content;
      //   }
      // )
    },
    watch: {
      "tooltipConfig.showTooltip": function (val) {
        // 当tooltip关闭后，如果是成功类型的说明操作成功了，此时一并将Window一起关闭
        if (val == false && this.tooltipConfig.tooltipType == "success") {
          this.windowConfig.show = false;
        }
      }
    },
    components: {
      IDatetimePicker,
      Checkbox,
      MyDialog,
      ImgUploader,
      IRadio,
      ISelect,
      ISlider,
      ISwitch,
      ITimePicker,
      ITransfer,
      MyTooltip,
      Tree,
      MyWindow,
      // TreeSelect,
      ITextarea,
      IInputbox,
      FileUploader,
      TabPane,
      Tabs,
      IForm,
      IFormItem,
      sideDialog,
      ISteps,
      IStep,
      IButton,
      IStepTabs,
      IStepTabPanel,
      IWindow,
      IProgress,
      ILoading,
      IOption
    }
  }
</script>

<style scoped>
  .normal-demo {
    font-family: "华文新魏";
    padding-top: 15px;
    background: #ffffff;
    border-top: 2px dashed lightgray;
    padding-bottom: 30px;
  }

  .title {
    width: 400px;
    font-size: 25px;
    text-shadow: 8px 8px 10px black;
    margin: 20px auto;
    text-align: center;
  }

  .common {
    font-family: "宋体";
    margin-left: 50px;
    padding-bottom: 15px;
  }

  .component-name {
    font-size: 20px;
    font-weight: bold;
  }

  .normal-component {
    padding: 3px;
    text-indent: 2em;
    font-size: 16px;
  }

  .description {
    color: #999999;
    padding-left: 33px;
  }

  .color {
    color: #fff;
    width: 100px;
    text-align: center;
    line-height: 30px;
    background-color: #ccc;
    border: 1px solid #000;
  }
</style>



