<template>
  <div class="nav">
    <my-menu v-for="cfg in menuList" @zoom="zoom" :config="cfg" :key="cfg.name" ref="menu"></my-menu>
  </div>
</template>

<script>
  import MyMenu from "./common/Menu.vue"
  import TreeUtil from "../utils/TreeUtil"

  export default {
    data() {
      return {
        menuList: []
      }
    },
    components: {
      MyMenu: MyMenu
    },
    methods: {
      zoom(menu) {
        for (var i in this.$refs.menu) {
          var vc = this.$refs.menu[i];

          if (vc.id != menu.id && vc.close) {
            vc.close();
          }
        }
      }
    },
    mounted() {
      var datas = new Array();
      var routes = this.$root.router.options.routes;
      var routeNames = new Array();

      for (var i in routes) {
        var routeName = routes[i].name;

        if (routeName && routeName.length
          && routeName.length > 0) {
          routeNames.push(routeName);
        }
      }

      //预置构造完整树的默认结点
      datas.push({
        id: "-1",
        name: ""
      });

      datas.push({
        id: "0",
        name: "",
        parentId: "-1"
      });

      var menuList = this.$root.tools.menuList();

      for (var i in menuList) {
        var data = menuList[i];
        var code = data.code;

        //只添加在在路由中配置了的菜单数据
        if (routeNames.indexOf(code) >= 0) {
          datas.push(data)
        }
      }

      this.menuList = TreeUtil(datas, ["code"]).children[0].children;
    }
  }
</script>

<style scoped>
  .nav {
    width: 100%;
    padding-top: 5px;
  }
</style>



