<template>
  <textarea class="textarea" v-model="textValue"></textarea>
</template>

<script>
  export default {
    props: {
      value: {
        type: String,
        default: ""
      }
    },
    data() {
      var textValue = this.value;

      return {
        textValue: textValue
      }
    },
    watch: {
      value(newValue) {
        this.textValue = newValue;
      },
      textValue(newValue) {
        this.$emit('input', newValue)
      }
    },
    name: "i-textarea"
  }
</script>

<style scoped>
  .textarea {
    outline: none;
    resize: none;
    width: 100%;
    overflow: auto;
    border: 1px solid #a9a9a9;
    border-radius: 4px;
    font-family: "Helvetica", "\5FAE\8F6F\96C5\9ED1", Arial, SimSun, sans-serif;
    color: #606266;
    font-size: 14px;
    padding: 8px;
  }

  .textarea:focus, .textarea:hover {
    border: 1px solid #5cadff;
    -webkit-box-shadow: 0px 0px 10px #d6ebff;
    -moz-box-shadow: 0px 0px 10px #d6ebff;
    box-shadow: 0px 0px 10px #d6ebff;
    cursor: text;
  }
</style>



