<template>
  <div class="pane" v-show="show">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'tabPane',
    props: {
      //设置pane的标识
      // name:{
      //     type:String
      // },
      //label是设置标题
      label: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        show: true
      }
    },
    methods: {
      updateNav: function () {
        this.$parent.updateNav();
      }
    },
    mounted() {
      this.updateNav();
    },
    watch: {
      label() {
        this.updateNav();
      }
    },
  }

</script>

<style scoped>


</style>



