<template>
  <div class="main_viewport">
    <!--<Headers></Headers>-->
    <div class="container">
      <div class="wrap">
        <!--<div class="nav-normal menubar" :class="{ 'nav-folded' : navFolded }">-->
          <!--<Navs></Navs>-->
        <!--</div>-->
        <div class="content">
          <transition name="fade" mode="out-in">
            <router-view></router-view>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import 'babel-polyfill';
import Navs from './components/Nav.vue';
import Headers from './components/Header.vue';

export default {
  name: 'app',
  data() {
    return {
      navFolded: false
    };
  },
  mounted() {
    var vue = this;

    this.$root.bus.$on('FoldNavBar', function() {
      vue.navFolded = true;
    });

    this.$root.bus.$on('UnfoldNavBar', function() {
      vue.navFolded = false;
    });
  },
  components: {
    Navs: Navs,
    Headers: Headers
  }
};
</script>

<style scoped>
.main_viewport {
  height: 100%;
}

.container {
  height: calc(100vh - 60px);
  overflow: auto;
}

.wrap {
  display: table;
  table-layout: fixed;
  height: 100%;
  width: 100%;
}

.menubar {
  vertical-align: top;
  background-color: #32404e;
  display: table-cell;
  overflow: hidden;
}

.content {
  vertical-align: top;
  background-color: #eeeeee;
  display: table-cell;
}

.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  -moz-transition: opacity 0.2s;
  -ms-transition: opacity 0.2s;
  -o-transition: opacity 0.2s;
  transition: opacity 0.2s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>



